import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data(){
		return {
			title: '意见反馈',
			opinion: '',
			phone: '',
			image: '', // 完整域名路径
			imgUrl: '', // 无域名
			isUpload: false
		}
	},
	methods:{
		
		// 提交
		subClick(){
			let userinfo = this.$store.state.userInfo
			let _this = this
			if(!(/^1[34578]\d{9}$/.test(this.phone))){
				uni.showModal({
					title: '提示',
					 content: '请输入正确的手机号'
				})
				return
			}
			if(this.opinion.length < 15){
				uni.showModal({
					title: '提示',
					content: '反馈意见需在15字以上！'
				})
				return
			}
			uni.request({
				url: this.$http + '/api/feedback/add',
				method: 'POST',
				data: {
					token: userinfo.token,
					phone: _this.phone,
					content: _this.opinion,
					images: _this.imgUrl
				},
				success(res){
					console.log('意见反馈', res)
					if(res.data.code == 1){
						uni.showToast({
							title: '提交成功'
						})
						_this.phone = ''
						_this.opinion = ''
						_this.image = ''
						_this.imgUrl = ''
						_this.isUpload = false
					}else{
						uni.showToast({
							title: '提交失败'
						})
					}
				}
			})
			
			console.log('意见', this.opinion, '手机', this.phone)
			
		},
		// 选择图片
		chooseImg(){
			
			let _this = this
			uni.chooseImage({
				count: 1, // 上传图片的张数
				sourceType: ['album'], //从相册选择
				success(res) { // 成功则返回图片的本地文件路径列表 tempFilePaths
					uni.showLoading({
						title: '上传中...'
					})
					const tempFilePaths = res.tempFilePaths;
					
					const uploadTask = uni.uploadFile({
						url: _this.$http + '/api/user/uphead',
						// methods: 'POST',
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {
							type: 'no',
							token: _this.$store.state.userInfo.token
						  },
						  success: function (uploadFileRes) {
						 console.log(uploadFileRes.data);
						 let resObj = JSON.parse( uploadFileRes.data )
						 // console.log(resObj);
						 if(resObj.code ==1){
							 uni.hideLoading()
							 console.log('上传成功')
							 _this.imgUrl = resObj.data.url
							 _this.image = resObj.data.http + resObj.data.url
							 _this.isUpload = true
							 // console.log(_this.image)
						 }
						}
						
					})
					
				}
			})
		},
		// 拨打电话
		callNumber(){
			uni.showLoading({
				title: '拨号中请稍后...'
			})
			uni.makePhoneCall({
				phoneNumber: '18284500858',
				success(res) {
					console.log(res)
					uni.hideLoading()
				}
			})
		},
			// 清除手机号
		clearPhone(){
			this.phone = ''
		}
	},
	components: {
		Topbar
	}
}
